.content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 100%;
  background-color: ivory;
}

.menu {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex: 3 1 0%;
      flex: 3 1 0%;
  margin: 10px;
  padding: 10px;
  background-color: beige;

}

.menuheader {
  background-color: #DEcaaf;
}

.cart {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex: 2 1 0%;
      flex: 2 1 0%;
  margin: 10px;
  padding: 10px;
  border: 0.5px dashed grey;
