  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  margin: 15px;
}
.menu .item button {
  /*display: flex;*/
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  height: 50%;
  margin: 15px;
}

.cart .item input {
  height: 50%;
  width: 10%;
  margin: 20px;
  text-align: center;
  font-size: 20px;
}

.right {
